import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getCourses } from '../../redux/actions/course';

const AvailableCourses = () => {
  const dispatch = useDispatch();
  const { courses } = useSelector(state => state.Courses);

  useEffect(() => {
    dispatch(getCourses());
  }, [dispatch]);

  return (
    <div className="container mx-auto px-4 py-6">
      <h2 className="text-2xl font-bold text-center mb-6">Cursos disponibles</h2>
      
      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {courses?.map(course => (
          <div
            key={course.id}
            className="bg-white shadow rounded-lg overflow-hidden border hover:shadow-lg transition-all duration-200"
          >
            {course.cover_image && (
              <img
                src={course.cover_image}
                alt={course.title}
                className="w-full h-40 object-cover"
              />
            )}
            <div className="p-4">
              <h3 className="text-lg font-semibold mb-1">{course.title}</h3>
              <p className="text-sm text-gray-500 mb-2">{course.category} | {course.language}</p>
              <p className="text-sm text-gray-700 mb-2">{course.description.slice(0, 100)}...</p>
              <p className="text-sm text-gray-600">Duración: {course.estimated_duration ?? 'N/A'} min</p>
              <p className="text-sm text-green-600 font-bold mt-2">XP: {course.xp_reward}</p>

              {/* Botón "Ver más" o "Inscribirse" */}
              <button
                className="mt-4 w-full bg-blue-500 text-white py-2 rounded hover:bg-blue-600 transition"
              >
                Ver más
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default AvailableCourses;
